@import url(https://fonts.googleapis.com/css?family=Press+Start+2P&display=swap);
div#page-home .link {
	text-decoration: none;
	color: var(--blue-1);
}

div#page-home .link:hover {
	color: var(--blue-2);
}

p#header {
	color: var(--blue-4);
	font-size: 10px;
	padding: 20px;
	text-align: center;
}

div#main {
	align-items: center;
	display: flex;
	flex-direction: column;
	height: 50vh;
	justify-content: center;
	margin-top: 100px;
}

div#main h1 {
	letter-spacing: 3px;
	line-height: 45px;
	text-align: center;
}

div#page-home #link-footer {
	bottom: 10px;
	display: flex;
	font-size: 16px;
	justify-content: center;
	left: 1px;
	padding: 20px;
	position: absolute;
	width: 99%;
}	

p[id^="number-"] {
	position: absolute;
	z-index: -1;
}

p#number-0 {
	-webkit-animation: numbers 1.2s infinite;
	        animation: numbers 1.2s infinite;
	color: var(--blue-2);
	left: 3em;
	top: 5em;
}

p#number-1 {
	-webkit-animation: numbers .8s infinite;
	        animation: numbers .8s infinite;
	bottom: 50%;
	color: var(--blue-1);
	font-size: 10px;
	left: 10%;
}

p#number-2 {
	-webkit-animation: numbers 1s infinite;
	        animation: numbers 1s infinite;
	color: var(--blue-2);
	font-size: 14px;
	right: 35%;
	top: 30%;
}

p#number-3 {
	-webkit-animation: numbers 1s infinite;
	        animation: numbers 1s infinite;
	bottom: 20%;
	color: var(--blue-2);
	font-size: 12px;
	left: 50%;
}

p#number-4 {
	-webkit-animation: numbers 1.4s infinite;
	        animation: numbers 1.4s infinite;
	color: var(--blue-3);
	font-size: 20px;
	right: 15%;
	top: 18%;
}

p#number-5 {
	-webkit-animation: numbers 1.4s infinite;
	        animation: numbers 1.4s infinite;
	bottom: 20%;
	color: var(--blue-3);
	font-size: 16px;
	left: 18%;
}

p#number-6 {
	-webkit-animation: numbers 1.6s infinite;
	        animation: numbers 1.6s infinite;
	bottom: 50%;
	color: var(--blue-3);
	font-size: 18px;
	right: 7%;
}

p#number-7 {
	-webkit-animation: numbers 1.8s infinite;
	        animation: numbers 1.8s infinite;
	color: var(--blue-4);
	font-size: 22px;
	left: 30%;
	top: 25%;
}

p#number-8 {
	-webkit-animation: numbers .8s infinite;
	        animation: numbers .8s infinite;
	color: var(--blue-1);
	font-size: 10px;
	left: 60%;
	top: 10%;
}

p#number-9 {
	-webkit-animation: numbers 1.8s infinite;
	        animation: numbers 1.8s infinite;
	bottom: 15%;
	color: var(--blue-1);
	font-size: 22px;
	right: 10%;
}

@-webkit-keyframes numbers {
	0% { 
		color: var(--blue-5);
		-webkit-transform: translateY(-200%);
		        transform: translateY(-200%); 
	}
	25% { color: var(--blue-3); }
	50% {
		color: var(--blue-1); 
		-webkit-transform: translateY(50%); 
		        transform: translateY(50%); 
	}
	75% { color: var(--blue-3); }
	100% { 
		color: var(--blue-5);
		-webkit-transform: translateY(-200%);
		        transform: translateY(-200%); 
	}
}

@keyframes numbers {
	0% { 
		color: var(--blue-5);
		-webkit-transform: translateY(-200%);
		        transform: translateY(-200%); 
	}
	25% { color: var(--blue-3); }
	50% {
		color: var(--blue-1); 
		-webkit-transform: translateY(50%); 
		        transform: translateY(50%); 
	}
	75% { color: var(--blue-3); }
	100% { 
		color: var(--blue-5);
		-webkit-transform: translateY(-200%);
		        transform: translateY(-200%); 
	}
}
.btn {
	background: transparent;
	border-radius: 5px;
	border: 1px solid var(--blue-1);
	color: var(--blue-1);
	font-family: 'Press Start 2P', cursive;
	font-size: 24px;
	margin-top: 40px;
	padding: 15px 60px;
	text-decoration: none;
	-webkit-transition: all .5s;
	transition: all .5s;
}

.btn:hover {
	background: var(--blue-1);
	color: var(--bg-1);
}

.btn.btn-primary {
	background: var(--blue-1);
	color: #fff;
}

.btn.btn-primary:hover {
	background: var(--blue-5);
	color: var(--blue-1);
}
div#load-page {
	display: flex;
	flex-direction: column;
	text-align: center;
	justify-content: space-between;
}

div#load-page h1#number-count {
	font-size: 4em;
	margin: 30vh 0;
}

div#load-page p {
	line-height: 24px;
	-webkit-animation: resize 1s ease-out infinite;
	        animation: resize 1s ease-out infinite;
	padding: 50px 0;
}

@-webkit-keyframes resize {
	0% { font-size: 1.1em; }
	50% { font-size: 1em; }
	100% { font-size: 1.1em; }
}

@keyframes resize {
	0% { font-size: 1.1em; }
	50% { font-size: 1em; }
	100% { font-size: 1.1em; }
}


div#random-number-page {
	text-align: center;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
}

div#random-number-page p {
	margin-top: 50px;
	font-size: 1.5em;
}

div#random-number-page p#number {
	margin-top: 0px;
	font-size: 3em;
}

div#actions {
	display: flex;
	justify-content: center;
}

.btn-action {
	background: transparent;
	border-radius: 5px;
	border: 1px solid var(--blue-1);
	color: var(--blue-1);
	font-family: 'Press Start 2P', cursive;
	font-size: 10px;
	height: 90px;
	line-height: 18px;
	margin: 0 5px;
	width: 90px;
	-webkit-transition: all .5s;
	transition: all .5s;
}

.btn-action:not(.btn-primary):hover {
	background: var(--blue-1);
	color: var(--bg-2);
}

.btn-action.btn-action-primary {
	background: var(--blue-2);
	color: #fff;
	line-height: 90px;
	text-decoration: none;
}

.btn-action.btn-action-primary:hover {
	background: var(--blue-5);
	color: var(--blue-1);
}




div#game-over-page {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	text-align: center;
}

div#game-over-page #play-again { 
	margin-top: 100px;
	display: flex;
	flex-direction: column;
	align-items: center; 
}

div#game-over-page #play-again p {
	font-size: 50px;
}

div#game-over-page #play-again .btn {
	font-size: 18px;
	padding-left: 0;
	padding-right: 0;
	width: 250px;
}

div#game-over-page #attempts { color: var(--blue-3); }
div#how-to-play-page {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	text-align: center;
	align-items: center;
}

div#how-to-play-page p {
	font-size: .8em;
	line-height: 16px;
}

div#how-to-play-page #en,
div#how-to-play-page #pt { margin: 0 20px; }

div#how-to-play-page #en p:first-child,
div#how-to-play-page #pt p:first-child {
	margin: 10px auto;
	font-size: 16px;
}

div#how-to-play-page .btn {
	width: 200px;
	margin-bottom: 20px;
	font-size: 16px;
}


div#easter-egg-page.screen {
	color: #49C583;
	border: 2px solid #3B9E69;
	background: url(/my-random-number/static/media/joker.51f98f05.png), radial-gradient(#9C60BB, #640695);
	background-repeat: no-repeat;
	background-size: 300px auto, 100% 100%;
	background-position: 50% 50%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	padding: 50px 0;
	font-size: 1.2em
}

div#easter-egg-page p#easter-egg {
	font-size: .8em;
}


:root {
	--blue-1: #00dfff;
	--blue-2: #00cbe8;
	--blue-3: #00a3ba;
	--blue-4: #008ea3;
	--blue-5: #003d46;
	--bg-1: #00101a;
	--bg-2: #000000;
}

* {
	margin: 0;
	padding: 0;
	outline: 0;
	box-sizing: border-box;
}

body {
	height: 100vh;
	max-width: 600px;
	background-image: radial-gradient(#00101a, #000000);
	background-image: radial-gradient(var(--bg-1), var(--bg-2));
	background-repeat: no-repeat;
	font-family: 'Press Start 2P', cursive;
	color: #00dfff;
	color: var(--blue-1);

}

div.screen {
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	height: 100%;
	max-width: 600px;
	width: 100%; 
	border: 2px solid #00dfff; 
	border: 2px solid var(--blue-1);
	border-radius: 5px;
}
